.add {
    width: 785px;
    display: flex;
    justify-content: space-between;
    margin: 10px 0 40px 130px;
}

.add input {
    width: 660px;
    outline: none;
    border: none;
    border-bottom: 2px solid #e7e7e7;
    font-size: 18px;
    color: #9c9696;
}

.add button {
    width: 110px;
    height: 33px;
    background-color: #0097f4;
    border-radius: 3px;
    border: none;
    outline: none;
    color: #fff;
    cursor: pointer;
    font-size: 18px;
}
