.container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.container > div {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 auto 20px;
    width: 730px;
}

.img  {
    width: 20px;
    cursor: pointer;
}

.search {
    position: relative;
    width: 540px;
}

.search input {
    border-top: none;
    border-left: none;
    border-right: none;
    outline: none;
    font-size: 18px;
    color: #9c9696;
    border-bottom: 1px solid #717171;
    margin-bottom: -7px;
    padding-left: 30px;
    box-sizing: border-box;
    width: 100%;
}

.search img {
    width: 20px;
    position: absolute;
    left: -23px;
}
