.Rebuild {
    position: fixed;
    right: 0;
    bottom: 0;
    margin-bottom: 28px;
    margin-right: 28px;
    width: 90px;
    height: 23px;
    background-color: #0097f4;
    border-radius: 3px;
    border: none;
    outline: none;
    color: #fff;
    cursor: pointer;
    font-size: 15px;
    z-index: 500;
}
