.container {
    position: fixed;
    top: 0;
    left: 0;
    z-index: 10000;
    background-color: rgba(0, 0, 0, 0.5);
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.delete {
    width: 225px;
    height: 115px;
    background-color: #eeeeee;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.delete__title {
    color: #000;
    font-size: 22px;
}

.delete__buttons {
    width: 180px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    justify-content: space-between;
}

.delete__button {
    width: 85px;
    height: 30px;
    background-color: #0097f4;
    color: #fff;
    font-size: 18px;
    border-radius: 1px;
    border: none;
    outline: none;
    cursor: pointer;
}

.delete__buttons_no {
    background-color: rgba(0, 0, 0, 0);
    color: #0097f4;
}

.edit {
    width: 320px;
    height: 250px;
    background-color: #eeeeee;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.edit span {
    width: 250px;
    font-size: 15px;
    display: flex;
}

.edit__title {
    color: #000;
    font-size: 22px;
}

.edit__area {
    width: 250px;
    height: 135px;
    outline: none;
    resize: none;
    padding: 10px;
    box-sizing: border-box;
    font-size: 15px;
    color: #000;
    border: none;
}

.edit__count {
    margin-left: 10px;
}

.edit__buttons {
    width: 180px;
    display: flex;
    justify-content: space-between;
}

.edit__button {
    width: 85px;
    height: 30px;
    background-color: #0097f4;
    color: #fff;
    font-size: 18px;
    border-radius: 1px;
    border: none;
    outline: none;
    cursor: pointer;
}

.edit__button_cancel {
    background-color: rgba(0, 0, 0, 0);
    color: #0097f4;
}
