.form {
    padding: 10px 15px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    min-height: 200px;
    margin-top: -100px;
}

.form legend {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 5px;
}

.form legend span {
    cursor: pointer;
}

.formItem {
    display: flex;
    flex-direction: column;
    width: 100%;
}

.formItem label {
    font-size: 15px;
}

.formItem input {
    width: 100%;
    height: 23px;
    margin: 5px 0;
    outline: none;
    padding-left: 5px;
}

.error {
    color: red;
}

.invalid {
    border: 1px solid red;
}

.valid {
}

.btn {
    width: 70%;
    margin: 5px auto;
    height: 30px;
    border: none;
    border-radius: 5px;
    color: #fff;
    font-size: 18px;
    cursor: pointer;
    background: #ccc;
    outline: none;
}

.btn[disabled] {
    cursor: not-allowed;
}

.btnRegister {
    background: #219c57;
}

.btnLogin {
    background: #4141c7;
}
