.container {

}

.Todo {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 910px;
    margin: 0 auto;
}

.Todo .text {
    width: 540px;
    min-height: 15px;
    background-color: #eeeeee;
    box-sizing: border-box;
    color: #000;
    font-size: 17px;
    overflow: hidden;
    padding: 20px 8px;
    margin: 5px;
}

.Todo .date {
    text-align: center;
}

.Todo .completed {
    background-color: #d1eef4;
}

.Todo .circle {
    border-radius: 50%;
    width: 40px;
    height: 40px;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
}

.Todo .edit {
    background-color: #0097f4;
}

.Todo .unchecked {
    background-color: #3f8c3f;
}

.Todo .checked {
    background-color: #8bc34a;
}

.Todo .delete {
    background-color: #f21818;
}

.Todo .priority {
    width: 45px;
    height: 30px;
    border-radius: 1px;
    background-color: #717171;
    color: #fff;
    font-size: 20px;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    justify-content: center;
    align-items: center;
}

.img {
    width: 20px;
}

.arrows {
    display: flex;
    flex-direction: column;
}

.arrow {
    width: 15px;
    cursor: pointer;
    margin: 3px 0;
}
